@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
/* search */
.search{position: relative; padding:11px 25px 15px;}
.search input{width:100%; border:1px solid #f1f2f4; padding:5px 0; text-align: center; border-radius: 5px; background: #f1f2f4; transition: border-color 0.2s linear 0s;}
.search input:focus{outline: none; border-color: yellow;}
.search i{position: absolute; top:16px; left:40px; color:#bbb;}

/* global */
body{font-family: 'Open Sans', sans-serif;}

main{-webkit-animation: 0.5s mainmove ease-in-out;animation: 0.5s mainmove ease-in-out;}

@-webkit-keyframes mainmove{
    0% {opacity: 0; transform: translateY(10px);}
    100% {opacity: 1;}
}

@keyframes mainmove{
    0% {opacity: 0; transform: translateY(10px);}
    100% {opacity: 1;}
}
/* navigation.css */
.tab_bar{position: fixed; bottom: 0; width: 100%; border-top: 1px solid #c1c1c3; background: #f7f7f7;
    display: flex; justify-content: space-between; padding: 10px 30px 5px;}
.tab_bar a{color: #c1c1c3; display: flex; flex-direction: column; align-items: center;
    text-decoration: none; transform: scale(1); transition: all 0.5s linear 0s;}
.tab_bar a:hover{transform: scale(1.1);}
.tab_bar a span{font-size: 10px; padding-top: 5px;}
.tab_bar i{font-size:22px;}
.tab_bar .choice{color:#513736;}
/* friends */
.friends a{text-decoration: none; color:#000;}
.section_h{font-size: 12px; padding:10px 15px; color:#b4b4b4; border-top:1px solid #ebebeb; border-bottom:1px solid #ebebeb;}
.section_row{display: flex; align-items: center; padding:10px 15px;}
.section_row a{font-size: 14px;}
.section_row span{font-size: 10px; padding: 10px; background: #f0f0f0; border-radius: 5px; color:#939393;}
.section_row img{width:40px; height: 40px; border-radius: 20px; margin-right:15px;}
.section_fri{justify-content: space-between;}

.s_column{display: flex; align-items: center;}

/* chats.css */
.chats{padding-bottom:54px;}

.chats_list{border-top:1px solid #ebebeb;}
.chats_list li{padding:15px; border-bottom:1px solid #ebebeb;}

.chats a{display: flex; justify-content: space-between;text-decoration: none; color:#000;}
.chats>a>span{font-size:10px; color:#b4b4b4;}

.chat_content{display: flex; align-items: center;}

.chat_content img{width:50px; height: 50px; border-radius: 50px; margin-right:15px;}
.chats_info h3{margin-bottom:5px; color:#000; font-weight: bold;}
.chats_info span{font-size:11px; color:#b4b4b4;}

/* talk_btn */
.talk_btn{display: flex; align-items: center; justify-content: center; position: fixed; right:20px; bottom:80px; width:60px; height:60px; background:#fde84d; border-radius: 30px; box-shadow: 0 3px 10px rgba(0,0,0,0.2); cursor: pointer; transition: color 0.3s, background 0.3s}
.talk_btn:hover{background:#523737;}
.talk_btn i{font-size: 20px; color:#523737;}
.talk_btn:hover i{color:#fff;}


/* find.css */
.find_options{display: flex; justify-content: space-between; padding:20px 30px 15px; background: #eee;}
.find_options div{display: flex; flex-direction: column; align-items: center; color:#532727;}
.find_options i{display: block; margin-bottom:10px; font-size: 1.3em;}
.find_options span{font-size:12px;}

.find header{padding:5px 10px; border-bottom:1px solid #ebebeb; font-size:12px; color:#b4b4b4;}
.recommend_none{padding:20px 15px; text-align: center; border-bottom:1px solid #ebebeb; color:#b4b4b4; font-size:14px;}
/* more.css */
.more{background: #f2f2f2;}
.more section{background: #fff;}
.more>header{display: flex; justify-content: space-between; padding:11px 15px 15px; background: #fff; margin-bottom:10px;}
.more>header>span{display: flex; align-items: center;}
.more>header>span>i{font-size:2em; color:#523737}
.user_contents{display: flex; align-items: center;}
.user_contents img{width:50px; border-radius: 25px; margin-right:20px;}
.user_content h3{font-weight: bold; color:#523737; margin-bottom:5px;}
.user_content span{font-size: 12px; color:#b4b4b4;}

.more_options{display: flex; justify-content: space-between; padding:18px 15px 15px; margin-bottom:10px;}
.more_options>div{display: flex; flex-direction: column; align-items: center; color:#523737;}
.more_options>div>i{font-size: 2em; margin-bottom:10px;}
.more_options>div>span{font-size:12px;}

.plus_friends{padding:20px; margin-bottom:10px;}
.plus_friends>header{display: flex; justify-content: space-between; margin-bottom:30px;}
.plus_friends>header>span{color:#b4b4b4;}
.plus_options{display: flex; flex-wrap: wrap;}
.plus_options>div{width:50%; margin-bottom:30px;}
.plus_options>div>i{margin-right:20px;}
.plus_options>div>span{font-size:13px;}

.more_link{display: flex; padding:20px;}
.more_link>div{display: flex; align-items: center; flex-direction: column; margin-right:20px;}
.more_link img{height: 45px; margin-bottom:10px;}
.more_link>div>span{font-size:12px;}

/* chat.css */
.chat_body{padding-bottom:0 !important;}

.chat_header{background: #a1c0d6;  color:#000; border-bottom:1px solid #91acc0;}
.chat_header a{color:#000;}
.chat{height: 87vh; background: #a1c0d6;}
.icon{font-size: 1.3em; line-height:0.75em;}

.chat{padding:12px 10px 0px;}
.data{text-align: center; font-size: 12px; opacity: 0.5; margin-bottom:15px;}

.chat_msg{display: flex; align-items: flex-end; margin-bottom:10px;}
.to_you{justify-content: flex-end;}
.chat_time{font-size:10px; opacity: 0.5;}
.message{display: flex; flex-direction: column;}
.message h3{font-size: 12px; font-weight: bold; margin-bottom:5px;}

.comment{padding:10px 5px; border-radius: 5px;}
.to_me>img{width:36px; height: 36px; border-radius: 18px; align-self: flex-start; margin-right:10px;}
.to_you .comment{background: #ffe934; align-self: flex-end;  margin:5px 0 5px 5px;}
.to_me .comment{background: #fff; align-self: flex-start; margin:5px 5px 5px 0;}

.chating{position: fixed; display: flex; justify-content: space-between; align-items: center; width:100%; bottom:0; height: 45px; box-sizing: border-box; padding:5px 10px; background: #eee;}
.chating>i{color:#b4b4b4;}
.chating_msg{position: relative; display: flex; justify-content: flex-end; align-items: center; width:80%;}
.chating_msg input{width:100%; padding:8px 10px;}
.chating_msg>i{position: absolute; right:40px; color:#b4b4b4;}
.chating_msg span{padding:8px 10px; background: #ffe934; color:#532727; cursor: pointer;}

/* profile.css */
.p_h{background: none;}

.profile_h{display: flex; position: relative; width:100%; height: 65vh; top:-63px; align-items: flex-end; justify-content: center;}
.profile_h>div{position: absolute; text-align: center; bottom:-60px;}
.profile_h>div>img{width:100px; height: 100px; border-radius: 50px; margin-bottom:10px;}

.container{margin-top:20px; display: flex; flex-direction: column; align-items: center;}
.container>input{padding:5px; margin-bottom:20px;}

.p_icons{display: flex; color:#532727;}
.p_icons>div{display: flex; flex-direction: column; align-items: center; margin-right:15px;}
.p_icons>div>span:nth-child(2){font-size:12px; font-weight: bold;}
.p_icon{display: flex; justify-content: center; align-items: center; width:50px; height: 50px; border-radius: 25px;  margin-bottom:15px; background: #f2f2f2;}
